import { Doughnut } from 'react-chartjs-2';
import React, {useState, useEffect} from "react";
// import React, {useState, useEffect, useRef} from "react";
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import ChartDataLabels from 'chartjs-plugin-datalabels';
import {RingSpinner} from "react-spinners-kit";
import axios from 'axios';


ChartJS.register(ArcElement, Tooltip, Legend, ChartDataLabels);


function Portfolio() {
    const [data, setData] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [current, setCurrent] = useState(0);

    const url = "/predict";

    useEffect(() => {
        const getData = async () => {
          try {
            const response = await axios.get(url);
            setData(response.data);
            setCurrent(response.data[0].current)
            setError(null);
          } catch (err) {
            setError(err.message);
            setData(null);
          } finally {
            setLoading(false);
          }
        };
        getData();
      }, []);

    // useEffect(() => {
    //     const timeout = setTimeout(() => {
    //         setLoading(true)
    //       }, 5000)
    //       return () => clearTimeout(timeout)
    //     }, [loading])

        
        if (loading) return(<center><p>{loading}</p><RingSpinner size={60} color="#a11d24" loading={true} /></center>)
    return (<div><center>
                    <Doughnut
                        width={350}
                        height={350}
                        data={{
                            labels: ['Stocks', 'Mutual Funds', 'Debt', 'Cash'],
                            datasets: [
                                {
                                    label: 'Asset allocation',
                                    data: [data[current].equity, data[current].mutual_fund, data[current].debt, data[current].cash],
                                    // data: [25,25,25,25],
                                    backgroundColor: ['#711116',
                                        '#9C0815',
                                        '#b8333a',
                                        '#c26066'
                                    ],
                                    hoverOffset: 30,
                                    borderColor: ['#000000',
                                        '#000000',
                                        '#000000',
                                        '#000000'
                                    ],
                                    borderWidth: 0.1,
                                },
                            ]
                        }}
                        options={{
                            radius: '85%',
                            responsive: false,
                            legend: {
                                position: 'Right',
                            },
                            animation: {
                                animateScale: true,
                                animateRotate: true
                            },
                            title: {
                                text: 'Asset Allocation',
                                display: true
                            },
                            plugins: {
                                datalabels: {
                                  color: "#ffffff",
                                  formatter: function (value) {
                                    return Math.round(value) + '%';
                                  },
                                  font: {
                                    weight: 'bold',
                                    size: 12,
                                  }
                                }
                              },
                        }}
                    /></center></div>
            );    
  }

export default Portfolio;